.slick-three-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .slider-three-wrapper{

        width: 176px;
        height: 350px;
        img{
            width: 176px;
            height: 264px;
            object-fit: cover;
            border-radius: 4px;
        }
        h3{
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 18px;
            color: #FFFFFF;
            padding-top: 16px;
        }
        span{
            font-style: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            color: #D3465B;
            display: block;
            padding-top: 6px;
        }
    }
}