.contents{
    height: 100%;
    background-color: #16151D;
    .slick-one {
        .slick-slider{
        position: relative !important;
        .slick-arrow{
           display: none;
        }
    }
    }
    .fuckkkkina{
        .slide-wrapper{
            width: 100vw;
            height: 100%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            display: flex;
            // align-items: center;
            flex-direction: column;
            // justify-content: center;
            position: relative;
            .container{
                &.fuck-slider{
                    position: relative;
                    left: 100px;
                    margin-top: 200px;
                }
                .buttons{
                    display: flex;
                    align-items: center;
                    .new-btn{
                        display: inline-block;
                        padding: 6px 12px;
                        background: #F4415F;
                        border-radius: 4px;
                        border: none;
                        outline: none;
                        font-style: normal;
                        font-weight: 600;
                        font-size: 12px;
                        line-height: 15px;
                        color: #FFFFFF;
                        margin-right: 14px;
                    }
                    .full-btn{
                        display: inline-block;
                        padding: 6px 12px;
                        background: #fff;
                        border-radius: 4px;
                        border: none;
                        outline: none;
                        font-style: normal;
                        font-weight: 600;
                        font-size: 12px;
                        line-height: 15px;
                        color: #2C2C2E;
                    }
                }
                .movie-title{
                    padding: 16px 0;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 48px;
                    line-height: 58px;
                    color: #FFFFFF;
                }
                .movie-info{
                    display: flex;
                    align-items: center;
                    .block-one{
                        display: flex;
                        align-items: center;
                        margin-right: 24px;
                        .movie-date, .movie-adults{
                            font-style: normal;
                            font-weight: 600;
                            font-size: 12px;
                            line-height: 15px;
                            color: #FFFFFF;
                        }
                        .movie-adults{
                            display: block;
                            padding-left: 3px;
                        }
                    }
                    .block-two{
                        display: flex;
                        align-items: center;
                        margin-right: 24px;
                        .img-one{
                            display: block;
                            padding-right: 8px;
                        }
                        .movie-vote{
                            font-style: normal;
                            font-weight: 600;
                            font-size: 18px;
                            line-height: 24px;
                            color: #FFFFFF;
                        }
                    }
                    .block-three{
                        display: flex;
                        align-items: center;
                        .img-two{
                            display: block;
                            padding-right: 8px;
                        }
                        .movie-count{
                            font-style: normal;
                            font-weight: 600;
                            font-size: 18px;
                            line-height: 24px;
                            color: #FFFFFF;
                        }
                    }
                }
                .subs-view{
                    display: flex;
                    align-items: center;
                    padding-top: 24px;
                    .subs-btn{
                        padding: 15px 24px;
                        background: #5931AC;
                        border-radius: 4px;
                        border: none;
                        outline: none;
                        font-style: normal;
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 17px;
                        color: #FFFFFF;
                        margin-right: 32px;
                    }
                    .fav-btn{
                        padding: 15px 24px;
                        background: rgba(60, 60, 67, 0.6);
                        border-radius: 4px;
                        border: none;
                        outline: none;
                        font-style: normal;
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 17px;
                        color: #FFFFFF;
                    }
                }
            }

            .bla-bla{
                background: linear-gradient(180deg, rgba(22, 21, 29, 0) 0%, rgba(22, 21, 29, 0.8) 48.31%, #16151D 100%);
                margin-top: 150px;
                .slick-two{
                    width: 80%;
                    
                    .slick-two-wrapper{
                        display: flex;
                        justify-content: space-between;
                        .slider-two-wrapper{
                            img{
                                width: 276px;
                                height: 168px;
                                object-fit: cover;
                                border-radius: 4px;
                            }
                            h1{
                                font-style: normal;
                                font-weight: 600;
                                font-size: 14px;
                                line-height: 18px;
                                color: #FFFFFF;
                                padding: 16px 0 ;
                            }
                        }
                    }
                }
            }
        }
    }

    .slick-four{
        margin-top: 50px;
        
    }
    .slick-five{
        margin-top: 50px;
    }

    .footer{
        margin-top: 80px;
        padding: 24px 0;
        background-color: #13121A;
    }
}

.title-movie{
    margin-bottom: 24px;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    color: #FFFFFF;
}

.slick-three, .slick-four, .slick-five{
    position: relative;
    .slick-arrow{
            position: absolute !important;
            top: -30px !important;
            &.slick-next{
                right: 25px !important;
                &:before{
                    border-radius: 50%;
                    color: #16151D;
                    padding: 5px;
                    background-color: #D3465B;
                }
            }
            &.slick-prev{
                position: absolute !important;
                left: 64rem !important;
                &:before{
                    border-radius: 50%;
                    color: #16151D;
                    padding: 5px;
                    background-color: #D3465B;
                }
            }
        }   
}